.game_page {
  background-color: #00296B;
  height: 100vh;
  width: 100vw;
  display: flex;
  flex-direction: column;
}

.game_timer {
  font-size: 100px;
  margin: 10px 50px;
  color: #ff0000;
}

.question_section {
  color: #07070A;
  height: 300px;
  margin: 50px 0 0;
  text-align: center;
}

.question_title {
  font-size: 50px;
  text-decoration: underline;
}

.question {
  font-size: 30px;
}

.answer_section {
  height: 100px;
  display: flex;
  align-items: center;
  justify-content: center;
}

.answer_btn {
  min-width: 200px;
  min-height: 80px;
  max-width: fit-content;
  max-height: fit-content;
  margin: 5px;
  background-color: #0C4B33;
  color: #f2f0f0;
  font-size: 20px;
}

.next_section {
  margin: 100px 0;
  display: flex;
  justify-content: center;
}

.next_btn {
  width: 100px;
  height: 40px;
  background-color: #f2f0f0;
  border: none;
  border-radius: 3px;
  font-size: 25px;
}

.wrong-answers {
  border: 3px solid #ff0000;
}

.correct-answer {
  border: 3px solid #06f00f;
}

@media screen and (max-width: 800px) {
  .game_page {
    background-color: #00296B;
    height: 100vh;
    width: 100vw;
    display: flex;
    flex-direction: column;
  }
  
  .game_timer {
    font-size: 80px;
    margin: 10px auto;
  }
  
  .question_section {
    height: fit-content;
    margin: 0;
  }
  
  .question_title {
    font-size: 40px;
    margin: 0 5px;
  }
  
  .question {
    font-size: 25px;
    margin: 25px 5px;
  }
  
  .answer_section {
    height: fit-content;
    flex-wrap: wrap;
  }
  
  .answer_btn {
    min-width: 150px;
    min-height: 60px;
    font-size: 20px;
    margin: 5px;
  }
  
  .next_section {
    margin: 10px 0;
  }
}