.feedback_page {
  background-color: #00296B;
  height: 90vh;
  width: 100vw;
  margin: 0;
}

.score_section {
  align-items: center;
  display: flex;
  justify-content: center;
  height: 100px;
  margin: 50px 0;
}

.assertions {
  font-size: 50px;
  margin-right: 50px ;

}

.total_score {
  font-size: 50px;
  margin-left: 50px ;
}

.feedback_section {
  align-items: center;
  display: flex;
  flex-direction: column;
  justify-content: center;
  height: 300px;
  margin: 50px 0;
}

.feedback_title {
  font-size: 40px;
}

.feedback_text {
  font-size: 25px;
  margin-bottom: 80px
}

.playAgain_btn {
  width: 100px;
  height: 40px;
  background-color: #0C4B33;
  border: none;
  font-size: 15px;
  color: #e1dddd;
}

@media screen and (max-width: 800px) {
  
  .score_section {
    align-items: center;
    display: flex;
    flex-direction: column;
    justify-content: center;
    height: 100px;
    margin: 70px 0;
  }
  
  .assertions {
    font-size: 40px;
    margin: 20px 0;
  
  }
  
  .total_score {
    font-size: 40px;
    margin: 0px;
  }
  
  .feedback_text {
    margin-bottom: 50px
  }
}